'use client';

import { useEffect, useRef, useState } from 'react';

interface AdBannerFallbackProps {
  id: string;
  template?: string;
  trackingCode?: string;
  width?: string;
  height?: string;
  fallbackAdSlot?: string;
}

declare global {
  interface Window {
    PartnersCoupang?: {
      G: new (config: {
        id: number;
        template: string;
        trackingCode: string;
        width: string;
        height: string;
      }) => void;
    };
    adsbygoogle?: any[];
  }
}

export default function AdBannerFallback({
  id = '912199',
  template = 'banner',
  trackingCode = 'AF9686108',
  width = '728',
  height = '90',
  fallbackAdSlot
}: AdBannerFallbackProps) {
  const [adStatus, setAdStatus] = useState<'loading' | 'coupang' | 'google' | 'error'>('loading');
  const containerRef = useRef<HTMLDivElement>(null);
  const scriptLoadedRef = useRef(false);

  useEffect(() => {
    if (scriptLoadedRef.current) return;
    scriptLoadedRef.current = true;

    const loadCoupangAd = () => {
      return new Promise<void>((resolve, reject) => {
        // ì¿ íŒ¡ ìŠ¤í¬ë¦½íŠ¸ ë¡œë“œ
        const script = document.createElement('script');
        script.src = 'https://ads-partners.coupang.com/g.js';
        script.async = true;

        script.onload = () => {
          try {
            // íƒ€ì„ì•„ì›ƒìœ¼ë¡œ ì‹¤íŒ¨ ì¼€ì´ìŠ¤ ì²˜ë¦¬
            const timeout = setTimeout(() => {
              reject(new Error('Coupang ad timeout'));
            }, 3000);

            // PartnersCoupang ê°ì²´ í™•ì¸ ë° ì‹¤í–‰
            const checkCoupang = () => {
              if (window.PartnersCoupang?.G) {
                clearTimeout(timeout);
                new window.PartnersCoupang.G({
                  id: parseInt(id),
                  template,
                  trackingCode,
                  width,
                  height
                });
                resolve();
              } else {
                // 100ms í›„ ì¬ì‹œë„
                setTimeout(checkCoupang, 100);
              }
            };
            checkCoupang();
          } catch (error) {
            reject(error);
          }
        };

        script.onerror = () => {
          reject(new Error('Failed to load Coupang script'));
        };

        document.head.appendChild(script);
      });
    };

    const loadGoogleAd = () => {
      return new Promise<void>((resolve, reject) => {
        // ê°œë°œ í™˜ê²½ì—ì„œëŠ” Google AdSenseë¥¼ ê±´ë„ˆë›°ê³  ë°”ë¡œ ì‹¤íŒ¨ ì²˜ë¦¬
        if (process.env.NODE_ENV === 'development') {
          console.warn('âš ï¸ Google AdSense skipped in development environment');
          reject(new Error('AdSense not supported in development'));
          return;
        }

        // êµ¬ê¸€ ì• ë“œì„¼ìŠ¤ ìŠ¤í¬ë¦½íŠ¸ ë¡œë“œ (í”„ë¡œë•ì…˜ë§Œ)
        const script = document.createElement('script');
        script.src = `https://pagead2.googlesyndication.com/pagead/js/adsbygoogle.js?client=ca-pub-6225662675575452`;
        script.async = true;
        script.crossOrigin = 'anonymous';

        script.onload = () => {
          try {
            // ê´‘ê³  ì‚½ì… div ìƒì„±
            if (containerRef.current && fallbackAdSlot) {
              const adDiv = document.createElement('ins');
              adDiv.className = 'adsbygoogle';
              adDiv.style.display = 'block';
              adDiv.setAttribute('data-ad-client', 'ca-pub-6225662675575452');
              adDiv.setAttribute('data-ad-slot', fallbackAdSlot);
              adDiv.setAttribute('data-ad-format', 'auto');
              adDiv.setAttribute('data-full-width-responsive', 'true');

              containerRef.current.appendChild(adDiv);

              // adsbygoogle ì´ˆê¸°í™”
              (window.adsbygoogle = window.adsbygoogle || []).push({});
            }
            resolve();
          } catch (error) {
            reject(error);
          }
        };

        script.onerror = () => {
          reject(new Error('Failed to load Google AdSense script'));
        };

        document.head.appendChild(script);
      });
    };

    // ì¿ íŒ¡ ê´‘ê³  ë¨¼ì € ì‹œë„
    loadCoupangAd()
      .then(() => {
        setAdStatus('coupang');
        console.warn('âœ… Coupang ad loaded successfully');
      })
      .catch((error) => {
        console.warn('âš ï¸ Coupang ad failed, trying Google AdSense:', error);

        // ì¿ íŒ¡ ì‹¤íŒ¨ ì‹œ êµ¬ê¸€ ì• ë“œì„¼ìŠ¤ë¡œ í´ë°±
        if (fallbackAdSlot) {
          loadGoogleAd()
            .then(() => {
              setAdStatus('google');
              console.warn('âœ… Google AdSense fallback loaded successfully');
            })
            .catch((googleError) => {
              console.error('âŒ Both ad systems failed:', googleError);
              setAdStatus('error');
            });
        } else {
          console.warn('âš ï¸ No fallback ad slot provided');
          setAdStatus('error');
        }
      });

  }, [id, template, trackingCode, width, height, fallbackAdSlot]);

  return (
    <div
      ref={containerRef}
      className="ad-banner-container flex justify-center items-center"
      style={{
        minWidth: adStatus === 'error' ? '0px' : width + 'px',
        minHeight: adStatus === 'error' ? '0px' : height + 'px',
        backgroundColor: 'transparent',
        display: adStatus === 'error' ? 'none' : 'flex'
      }}
    >
      {/* ë¡œë”© ìƒíƒœì™€ ì—ëŸ¬ ìƒíƒœ ëª¨ë‘ ìˆ¨ê¹€ ì²˜ë¦¬ */}
    </div>
  );
}